import React from "react";
import { Transition, Icon, Button, Grid } from "semantic-ui-react";
import dii_casses from "../images/avatars/dii_casses.png";
import equinox from "../images/avatars/equinox.png";
import shakil from "../images/avatars/shakil.png";
import galileo from "../images/avatars/galileo.png";
import diconcilio from "../images/avatars/diconcilio.png";
import lovely from "../images/avatars/lovely.png";

const avatars = [
  {
    id: 10,
    name: "Dii Casses",
    role: "Creator",
    image: dii_casses,
    discord: "dii#3452",
  },
  { id: 1, name: "Diconcilio", role: "Artist", image: diconcilio },
  {
    id: 2,
    name: "Equinox",
    role: "Blockchain Developer",
    image: equinox,
    discord: "Equinox#1175",
  },
  {
    id: 3,
    name: "Shakil",
    role: "Web Developer",
    image: shakil,
    discord: "Shakil#6717",
  },
  {
    id: 4,
    name: "Galileo",
    role: "Moderator",
    image: galileo,
    discord: "galieo#4689",
  },
  {
    id: 5,
    name: "Lovely",
    role: "Marketer",
    image: lovely,
  },
];

export default function TeamMembers() {
  const [open, setOpen] = React.useState(false);
  const [visible, setVisible] = React.useState(false);
  function toggleVisibility() {
    setOpen(!open);
    setVisible(!visible);
  }
  return (
    <div style={{ marginTop: 20 }}>
      <Button
        fluid
        onClick={toggleVisibility}
        className={"faq-button" + (visible ? " opened" : "")}
        basic
      >
        <span>Team</span>
        <div className="trw">
          <Transition
            visible={visible}
            animation="vertical flip"
            duration={200}
          >
            <Icon
              color={visible ? "green" : "black"}
              name="angle up"
              size="large"
            />
          </Transition>
        </div>
        <div className="trw">
          <Transition
            visible={!visible}
            animation="vertical flip"
            duration={200}
          >
            <Icon name="angle down" size="large" />
          </Transition>
        </div>
      </Button>
      <Transition animation="slide down" visible={visible}>
        <Grid columns={2} centered container stackable stretched>
          {avatars.map((avatar) => (
            <Grid.Column key={avatar.id} style={{ alignItems: "center" }}>
              <div className="team-member">
                <img src={avatar.image} alt={avatar.name} />
                <div className="team-text">
                  <p>{avatar.name}</p>
                  <p className="position">{avatar.role}</p>
                  {!!avatar.discord && (
                    <p className="discord">
                      <Icon name="discord" /> {avatar.discord}
                    </p>
                  )}
                </div>
              </div>
            </Grid.Column>
          ))}
        </Grid>
      </Transition>
    </div>
  );
}
