import React from "react";
import { Helmet } from "react-helmet";

const desc =
  "r favorite toilet dwelling creatures r back & back with a vengeance! (fire emoji) Ready to take over the Web3 world with their undeniable addictive slimy cuteness.";

const title = "OOZ1ES - 100% on-chain NFT project on the Cardano blockchain";

export default function MetaTags() {
  return (
    <Helmet>
      <html lang="en" />
      <title>OOZ1ES - Booger is back and cuter than ever!</title>
      <link rel="icon" href="/ft/o_logo.png" type="image/png" />
      <meta name="title" content={title} />
      <meta name="description" content={desc} />

      <meta property="og:type" content="website" />
      <meta property="og:url" content="https://2.ooz1es.io/" />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={desc} />
      <meta property="og:image" content="/ft/o_logo.png" />
      <meta property="twitter:card" content="summary_large_image" />
      <meta property="twitter:url" content="https://2.ooz1es.io/" />
      <meta property="twitter:title" content={title} />
      <meta property="twitter:description" content={desc} />
      <meta property="twitter:image" content="/ft/o_logo.png" />
    </Helmet>
  );
}
